@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700&display=swap);
 
 #header{ 
     background-image: url(/crowdfunding-product-page-main/static/media/image-hero-mobile.d4f477d4.jpg);
     width: 100%;
     padding-bottom: 200px;
     background-repeat: no-repeat;
     background-size: cover;
 }
 .header-section {
     padding: 2rem 0rem;
     position: relative;
     width: 90%;
     margin: 0 auto;
 }
 #logo{
    min-width: 15%;
    max-height: 2rem;
    float: left;

 }
 #iconHam-Close{
     max-width: 35px;
     max-height: 35px;
     float: right;
 }
#menu {
    position: absolute;
    top: 0;
    top: 5rem;
    background-color: white;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    border-radius: 10px;
    z-index: 3;
}
#menu p{
    padding-left: 2rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}
.hide{
    visibility: hidden;
} 
.show {
    visibility: visible;
}
#body-top{
    display: grid;
    place-items: center;
    text-align: center;
    position: relative;
    top: -30px;
}
#body-top h1 {
    padding: 1rem 2rem;
    max-width: 30ch;
 }
 #body-top p {
    padding: 1rem 2rem;
    max-width: 45ch;
    color: var(--DarkGray);
 }
 #body-top div button{
    float: left;
    background-color: var(--ModerateCyan);
    min-width: 190px ;
    max-width: 66%;
    padding: 1rem 2rem;
    color: white;
    border-radius: 2rem;
    border: none;
    font-weight: 700;
 }
 #body-top div img{
     float: right;
     margin-left: 1rem;
     border-radius: 60%;
     
    }

#pageBody{
    position: relative;
    top: -3rem;
    width: 90%;
    margin: 0 auto;
    background-color: white;
    border-radius: 10px;
}
#app{
    position: relative;
}
#modal{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

#modal-content{
    margin: 5rem auto;
    width: 92%;
    background-color: white;
    border-radius: 10px;
    z-index: 2;
    padding-bottom: 3rem;
}
#modal-close{
    position:absolute;
    height: 1rem;
    right: 0;
    top: 25%;
}
.back-project-top {
    display: -webkit-flex;
    display: flex;
    position: relative;
}
.back-project-top h3{
    font-size: 1.4rem;
    font-weight: 700;
    padding: 1.2rem 0rem;
    padding-right: 2rem;
}
#back-project {
    width: 85%;
    margin: 2rem auto;
}
#back-project p{
    font-size: 1.1rem;
    font-weight: 400;
    padding: 1.2rem 0rem;
    color: var(--DarkGray);
    max-width: 80ch;
}
#modal-success{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}
#modal-success-content{
    margin: 5rem auto;
    width: 92%;
    background-color: white;
    border-radius: 10px;
    z-index: 5;
    padding-bottom: 3rem;
    display: grid;
    place-items: center;
    text-align: center;
}
#modal-success-content img{
    margin-top: 1rem;
}
#modal-success-content button{
    padding: 1rem 2rem;
    border-radius: 2rem;
    background-color: var(--ModerateCyan);
    color: white;
}
#modal-success-content p{
   margin: 1rem 0;
   color: var(--DarkGray);
   font-size: 1.1rem;
   max-width: 70ch;
}
#modal-success-content h2{
    margin: 1rem 0;
    font-size: 1.6rem;
}
#stats{
    margin-top: 2rem;
    background-color: whitesmoke;
    display: -webkit-flex;
    display: flex;
    place-items: center;
    padding: 1rem 1.5rem;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.stat{
    text-align: center;
    min-width: 20ch;
    padding: 1rem;
}
.stat h4{
    font-size: 2rem;
    font-weight: 700;
}
.stat p{
    font-size: 0.9rem;
    font-weight: 400;
    color: var(--DarkGray);
}
.bar{
    width: 100%;
    background-color: var(--DarkGray);
    height: 2rem;
    position: relative;
}
.bar-progress {
    background-color: var(--DarkCyan);
    height: 2rem;
    position: absolute;
}
#progress-container{
    width: 100%;
}
#about{
    margin-top: 3rem;
    padding: 1rem 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
#about h3{
    font-size: 2rem;
    font-weight: 700;
    padding: 1.2rem 0rem;
}
#about p{
    font-size: 1.3rem;
    font-weight: 400;
    padding: 1.2rem 0rem;
    color: var(--DarkGray);
    max-width: 80ch;
}
.reward{
    width: 90%;
    padding: 1rem 1.5rem;
    margin: 1.5rem auto;
    border-radius: 10px;
}
.selected {
    border: 3px solid var(--DarkCyan);
}
.no-selected{
    border: 1px solid var(--DarkGray);
}
.reward h3{
    font-size: 1rem;
    font-weight: 700;
    padding: 1rem 0;
}
.reward h4{
    font-size: 1rem;
    font-weight: 700;
    color: var(--ModerateCyan);
    margin-bottom: 1rem;
}
.reward p{
    font-size: 0.9rem;
    font-weight: 500;
    color: var(--DarkGray);
    padding: 1.5rem 0;
}
.reward strong{
    font-size: 3rem;
    color: black;
    padding-right: 1rem;
}
.reward button {
    color: white;
    font-weight: 700;
    font-size: 1rem;
    padding: 1.5rem 2.5rem;
    border-radius: 2.5rem;
}
.stock {
    background-color: var(--DarkCyan);
}
.no-stock{
    background-color: var(--DarkGray);
}
.submit-btn{
    background-color: var(--DarkCyan);
    float: right;
    padding: 1.5rem 1.5rem;
    border-radius: 1.5rem;
}
.grey-filter{
    -webkit-filter:opacity(0.5);
            filter:opacity(0.5);
}
.outro{
    padding: 1rem;
    border: 1px solid grey;
    width: 1rem;
    height: 1rem;
    border-radius: 60%;
    position: relative;
    margin-right: 2rem;
}
.inner{
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    padding: 0.5rem;
    border-radius: 60%;
    top: 25%;
    left: 25%;
}
.pledge-header{
    -webkit-align-items: center;
            align-items: center;
}
.pledge-input-container{
    min-width: 4rem;
    margin-right: 0.5rem;
    padding: 1rem;
    border: 1px solid var(--DarkGray) ;
    border-radius: 1rem;
    position: relative;
}
.pledge-input{
    width: 60%;
    border: none;
}
.reward form{
    border-top: 2px solid gray;
    display: grid;
    place-items: center;
    text-align: center;
    padding-bottom: 3rem;
}
.error-message{
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 2rem;
    color: red;
    font-size: 0.9rem;
    font-weight: 500;
    padding: 1.5rem 0;
}
:root {
 --ModerateCyan : hsl(176, 50%, 47%);
 --DarkCyan: hsl(176, 72%, 28%);
  --Black:  hsl(0, 0%, 0%);
  --DarkGray: hsl(0, 0%, 48%);
  --Font: Commissioner;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
   font-family: Commissioner;
   font-family: var(--Font);
}



.attribution { font-size: 11px; text-align: center; }
.attribution a { color: hsl(228, 45%, 44%); }
